<template>
  <div>
    <div>
      <br>
      <span>用户名:</span>
      <el-input style="width:20%" v-model="userName" placeholder="请输入内容" value='admin'> </el-input>
    </div>
    <br>
    <div>
      <span>用户名:</span>
      <el-input style="width:20%" v-model="userName" placeholder="请输入内容" value='admin'> </el-input>
    </div>
    <div>
      <span>个人头像：</span>
      <el-upload class="avatar-uploader" action="https://jsonplaceholder.typicode.com/posts/" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
    </div>
    <div>
      <span>个人简介：</span>
      <el-input type="textarea" style="width:40%; vertical-align: top" :rows="6" placeholder="请输入内容" v-model="textarea">
      </el-input>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      imageUrl: '',
      userName: '',
      textarea: ''
    };
  },
  methods: {
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    }
  }
}
</script>
<style>
.avatar-uploader{
  display: inline-block;
  vertical-align: top;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.avatar-uploader .el-upload:hover {
  border-color: #20a0ff;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.avatar {
  width: 178px;
  height: 178px;
  display: inline-block;
}
</style>


